 export const DarkSoulShareButtons = (props) => {
    const {iconColor} = props;

    //make dynamic!
    let url = typeof window !== "undefined" ? "https://yle.fi" : "https://yle.fi";

    return (
            <div className={`social-container ${props.centered ? "centered" : ""}`}>
                <a href={"https://www.facebook.com/sharer/sharer.php?u=" + encodeURIComponent(url)} target="_blank"><Facebook fill={iconColor} /></a>
                <a href={"https://twitter.com/intent/tweet?original_referer=" + encodeURIComponent(url) + "&ref_src=twsrc%5Etfw&text=" + encodeURIComponent("") + "&tw_p=tweetbutton&url=" + encodeURIComponent(url)} target="_blank"><Twitter fill={iconColor} /></a>
                <a href={"whatsapp://send?text=" + encodeURIComponent("") + encodeURIComponent(url) + "&display=popup&ref=plugin&src=like&kid_directed_site=0"} target="_blank"><Whatsapp fill={iconColor} /></a>
            </div>
        
    )
} 